<template>
  <div class="search">
    <!-- 搜索框 -->
    <input
      type="text"
      placeholder="搜索歌曲、歌手、专辑"
      v-model="value"
      @keyup.enter="saveHistory"
      @focus="
        resSong = [];
        isResult = false;
      "
      :saveHistory="saveHistory"
      ref="searchinput"
    />
    <svg
      t="1663728100517"
      class="icon"
      viewBox="0 0 1024 1024"
      version="1.1"
      xmlns="http://www.w3.org/2000/svg"
      p-id="2367"
      width="200"
      height="200"
    >
      <path
        d="M469.333 192c153.174 0 277.334 124.16 277.334 277.333 0 68.054-24.534 130.411-65.216 178.688L846.336 818.24l-48.341 49.877L630.4 695.125a276.053 276.053 0 0 1-161.067 51.542C316.16 746.667 192 622.507 192 469.333S316.16 192 469.333 192z m0 64C351.51 256 256 351.51 256 469.333s95.51 213.334 213.333 213.334 213.334-95.51 213.334-213.334S587.157 256 469.333 256z"
        p-id="2368"
        fill="rgb(191, 191, 191)"
      />
    </svg>
    <span
      v-show="!isSee"
      @click="
        value = '';
        resSong = [];
      "
      >×</span
    >

    <!-- 热门搜索和历史记录 -->
    <div class="line"></div>
    <p v-if="isSee">热门搜索</p>
    <ul v-if="isSee" class="dis">
      <SearchItem
        v-for="search in searchRes"
        :key="search.id"
        :search="search"
        @clickSearch="clickHandler(search.first)"
      />
    </ul>
    <ul class="history" v-if="resSong && isSee">
      <li
        class="history-li"
        v-for="(h, i) in history"
        :key="i"
        @click="clickHandler(h)"
      >
        {{ h }}
        <i @click.stop="deleteLocal(h)">×</i>
      </li>
    </ul>

    <!-- 搜索中 -->
    <SearchRes
      :keywords="keywords"
      :value="value"
      v-if="!isSee && !isResult"
      @song="
        value = $event;
        isResult = true;
        saveHistory();
      "
    />

    <!-- 搜索结果 -->
    <div class="searchSong" v-if="isResult && !isSee">
      <p v-show="resSong.length !== 0" class="best">最佳匹配</p>
      <SearchSingerCard :singer="singer" :cd="cd" />
      <ul>
        <SearchSong
          v-for="end in resSong"
          :key="end?.id"
          :song="end"
          :resSong="resSong"
          @clickHandler="$emit('clickHandler', $event)"
          :autoplay="autoplay"
          :res="res"
        />
      </ul>
    </div>
  </div>
</template>

<script>
import SearchItem from "@/components/SearchItem.vue";
import SearchRes from "@/components/SearchRes.vue";
import SearchSong from "@/components/SearchSong.vue";
import SearchSingerCard from "@/components/SearchSingerCard.vue";
export default {
  components: {
    SearchItem,
    SearchRes,
    SearchSong,
    SearchSingerCard,
  },
  props: ["res", "autoplay"],
  data: function () {
    return {
      searchRes: [],
      value: "",
      keywords: [],
      history: JSON.parse(localStorage.getItem("song")) || [],
      resSong: [],
      isResult: "",
      singer: [],
      cd: null,
    };
  },
  computed: {
    isSee() {
      if (this.value == "") {
        return true;
      } else {
        return false;
      }
    },
    // isSearch() {
    //   if (this.value == "") {
    //     return true;
    //   } else {
    //     return false;
    //   }
    // },
  },
  methods: {
    clickSearchItem: function (s) {
      // console.log(123);
      // console.log(s);
      // this.history.unshift(this.value);
      this.value = s;
      this.axios
        .get("https://apis.netstart.cn/music/cloudsearch", {
          params: {
            keywords: this.value,
            limit: 30,
            offset: 0,
          },
        })
        .then((res) => {
          // console.log(res.data);
          this.isResult = true;
          this.resSong = res.data.result.songs;
          this.$emit("updata-loading");
        });
      this.axios("https://apis.netstart.cn/music/search/multimatch", {
        params: {
          keywords: this.value,
        },
      }).then((res) => {
        // console.log(res);
        this.singer = res.data.result.artist;
        this.cd = res.data.result.album;
        this.$emit("updata-loading");
      });
      // return this.search.first;
      // 错误写法
    },
    saveHistory: function () {
      // console.log(123);
      // console.log(this.value);
      if (this.$refs.searchinput.value != "") {
        this.history.unshift(this.value);
        // console.log(new Set(this.history));

        this.$refs.searchinput.blur();
        this.axios
          .get("https://apis.netstart.cn/music/cloudsearch", {
            params: {
              keywords: this.value,
              limit: 30,
              offset: 0,
            },
          })
          .then((res) => {
            // console.log(res.data);
            this.isResult = true;
            this.resSong = res.data.result.songs;
            this.$emit("updata-loading");
          });
        this.axios("https://apis.netstart.cn/music/search/multimatch", {
          params: {
            keywords: this.value,
          },
        }).then((res) => {
          // console.log(res.data.result);
          this.singer = res.data.result.artist;
          this.cd = res.data.result.album;
          this.$emit("updata-loading");
        });
      }
    },
    clickHandler: function (n) {
      // console.log(n);
      // this.value = n;
      this.axios
        .get("https://apis.netstart.cn/music/cloudsearch", {
          params: {
            keywords: n,
            limit: 30,
            offset: 0,
          },
        })
        .then((res) => {
          // console.log(res.data);
          this.isResult = true;
          this.resSong = res.data.result.songs;
          this.$emit("updata-loading");
        });
      this.axios("https://apis.netstart.cn/music/search/multimatch", {
        params: {
          keywords: n,
        },
      }).then((res) => {
        this.singer = res.data.result.artist;
        this.cd = res.data.result.album;
        this.$emit("updata-loading");
      });
      this.history.unshift(n);
      this.history = [...new Set(this.history)];
      this.value = n;
    },
    getKeyworlds: function (n) {
      this.axios
        .get("https://apis.netstart.cn/music/search/suggest", {
          params: {
            keywords: n,
            type: "mobile",
          },
        })
        .then((res) => {
          // console.log(res.data.result.allMatch);
          // console.log(res);
          // this.keywords = res.data.result.allMatch;
          this.keywords = res.data.result.allMatch;
          this.$emit("updata-loading");
        });
    },
    deleteLocal: function (n) {
      // console.log(123);
      // console.log(this.history);
      // console.log(n);
      this.history = this.history.filter((e) => {
        return e !== n;
      });
      localStorage.setItem("song", JSON.stringify(this.history));
    },
  },
  created() {
    this.axios.get("https://apis.netstart.cn/music/search/hot").then((res) => {
      //   console.log(res.data.result.hots);
      this.searchRes = res.data.result.hots;
      this.$emit("updata-loading");
    });
    // this.axios
    //   .get("https://apis.netstart.cn/music/search/suggest", {
    //     params: {
    //       keywords: n,
    //       type: "mobile"
    //     }
    //   })
    //   .then(res => {
    //     // console.log(res.data.result.allMatch);
    //     console.log(res);
    //   });
  },
  watch: {
    value: function (val) {
      if (val !== null) {
        this.axios
          .get("https://apis.netstart.cn/music/search/suggest", {
            params: {
              keywords: val,
              type: "mobile",
            },
          })
          .then((res) => {
            // console.log(res.data.result.songs);
            // console.log(res);
            // console.log(res.data.result);
            this.keywords = res.data.result;
            this.$emit("updata-loading");
          });
      }
    },
    history(newhistory) {
      localStorage.setItem("song", JSON.stringify(newhistory));
    },
    // value: function(n) {
    //   console.log(n);
    //   if (n) {
    //     this.getKeyworlds(n);
    //   } else {
    //     // this.getKeyworlds = [];
    //   }
    // }
  },
  // hide: function(n) {
  //   if (n == !null) {
  //     this.value.style.display = "none";
  //   } else {
  //     this.value.style.display = "block";
  //   }
  // }
};
</script>

<style lang="less" scoped>
.search {
  input {
    width: 350rem;
    margin: 15rem 0;
    margin-left: 10rem;
    height: 30rem;
    padding: 0 35rem;
    // position: absolute;
    border-radius: 20rem;
    border: none;
    background-color: rgba(242, 242, 242, 0.821);
    &::-webkit-input-placeholder {
      color: rgb(191, 191, 191);
    }
    &:focus {
      outline: none;
    }
  }
  svg {
    width: 20rem;
    height: 20rem;
    position: absolute;
    left: 17rem;
    top: 140rem;
  }
  span {
    top: 140rem;
    right: 25rem;
    position: absolute;
    width: 20rem;
    height: 20rem;
    background-color: rgba(173, 173, 173, 0.211);
    color: grey;
    display: block;
    border-radius: 20rem;
    text-align: center;
    line-height: 20rem;
    font-size: 20rem;
    font-weight: 600;
  }
  .line {
    width: 100%;
    border-top: 1rem solid rgb(230, 230, 230);
  }
  p {
    margin-top: 10rem;
    margin-left: 15rem;
    font-size: 16rem;
    font-weight: 600;
  }
  .dis {
    display: flex;
    flex-wrap: wrap;
  }
  .history {
    margin-top: 25rem;
    margin-left: 10rem;
    // margin: 0 10rem;
    // margin-right: 10rem;
    // padding: 50rem 0;
    .history-li {
      // width: 100vw;
      height: 20rem;
      font-size: 16em;
      line-height: 18rem;
      margin-left: 10rem;
      padding-left: 30rem;
      margin-bottom: 15rem;
      margin-right: 20rem;
      // padding-bottom: 20rem;
      background-image: url(data:image/svg+xml;base64,);
      background-repeat: no-repeat;
      // padding: 20rem;
      border-bottom: 1rem solid rgba(230, 230, 230, 0.411);
      i {
        position: absolute;
        right: 20rem;
        font-weight: 600;
        font-size: 20rem;
      }
    }
  }
  .best {
    margin-top: 10rem;
    margin-left: 10rem;
  }
}
</style>
